.helpers__nut-container {
  display: flex;
  justify-content: center;
  align-items: center;
  // background-color: #b6b6b67e;
  color: white;
  font-weight: 700;
  letter-spacing: 0.2em;
  height: 3em;
  width: 15em;
  border-radius: 0.5em;
  box-shadow: inset 5px -8px 12px rgb(255 255 255 / 50%),
    inset 10px 15px 25px rgb(66 65 65 / 70%);
}

.helper-valve__line-container {
  align-items: center;
  display: grid;
  grid-template-rows: 1.5fr 4fr;
}

.helper-valve__main-line {
  background-color: #686868;
  color: rgb(94, 90, 90);
  height: 1.5em;
  box-shadow: 0px 0px 0px #eee, 0px 0px 0px #7c7c7c, inset 0px 15px 12px #eee,
    inset 0px 15px 25px #7c7c7c;
}

.helper-valve__line {
  grid-row: 1/2;
  width: 1em;
}

.helper-valve__valve {
  grid-row: 1/2;
}
